<template>
  <el-table :data="songs" style="width: 100%">
    <el-table-column type="index" label="#"></el-table-column>
    <!-- <el-table-column prop="fee" width="50px"></el-table-column> -->
    <el-table-column width="50px">
      <template slot-scope="scope">
        <button @click="play(scope.row)" type="button">
          <i class="el-icon-video-play"></i>
        </button>
      </template>
    </el-table-column>
    <el-table-column prop="name" label="歌曲"></el-table-column>
    <el-table-column label="歌手">
      <template slot-scope="scope">
        <router-link
          v-for="(item, index) in scope.row.ar"
          :key="index"
          :to="`/artist/${item.id}`"
          >{{ item.name }}
        </router-link>
      </template>
    </el-table-column>
    <el-table-column label="专辑">
      <template slot-scope="scope">
        <router-link :to="`/album/${scope.row.al.id}`"
          >{{ scope.row.al.name }}
        </router-link>
      </template>
    </el-table-column>
    <el-table-column label="时长">
      <template slot-scope="scope">
        <span>{{ scope.row.dt | timeFormat }}</span>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  props: ["songs"],
  methods: {
    play(data) {
      this.$store.commit("setSingleList", data);
    },
  },
};
</script>

<style lang="less" scoped>
.el-table {
  color: #000000;
  button {
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    &:hover{
      color: #409EFF;
    }
    i {
      font-size: 25px;
    }
  }
}
</style>